.todo__visible {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  display: flex;
  border: 1px solid rgb(235, 235, 235);
  background-color: white;
  width: 800px;
  height: 50px;
  margin: auto;
  margin-bottom: 70px;
}
.todo__visible button {
  cursor: pointer;
  font-family: "Ubuntu", sans-serif;
  font-size: 14px;
  outline: none;
  border: 1px solid white;
  border-radius: 20px;
  margin: 5px;
  width: 90px;
  height: 33px;
  color: rgb(100, 100, 100);
  background-color: white;
  transition: all 0.3s ease;
}
.todo__visible button:hover {
  border: 1px solid rgb(210, 210, 210);
}
.todo__visible button:focus {
  background-color: rgb(230, 230, 230);
  border: 1px solid rgb(230, 230, 230);
}
.todo__visible p {
  cursor: pointer;
  position: absolute;
  right: 20px;
  font-size: 14px;
  color: rgb(100, 100, 100);
  transition: all 0.1s ease;
}
.todo__visible p:hover {
  text-decoration: underline;
  color: rgb(255, 100, 100);
}

@media screen and (max-width: 768px) {
  .todo__visible {
    width: 350px;
  }
  .todo__visible button {
    position: relative;
    right: 55px;
    font-size: 8px;
    width: 65px;
    height: 27px;
  }
  .todo__visible p {
    right: 10px;
    font-size: 8px;
    top: 11px;
  }
}
